@font-face {
    font-family: "Noto Sans CJK KR";
    font-weight: 200;
    src: url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Thin.woff2") format("woff2"),
    url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Thin.woff") format("woff"),
    url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Thin.otf") format("truetype");
}
@font-face {
    font-family: "Noto Sans CJK KR";
    font-weight: 300;
    src: url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Light.woff2") format("woff2"),
    url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Light.woff") format("woff"),
    url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Light.otf") format("truetype");
}
@font-face {
    font-family: "Noto Sans CJK KR";
    font-style: normal;
    font-weight: 400;
    src: url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Regular.woff2") format("woff2"),
    url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Regular.woff") format("woff"),
    url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Regular.otf") format("truetype");
}
@font-face {
    font-family: "Noto Sans CJK KR";
    font-weight: 500;
    src: url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Bold.woff2") format("woff2"),
    url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Bold.woff") format("woff"),
    url("./styles/fonts/Noto_Sans_KR/NotoSansKR-Bold.otf") format("truetype");
}

* {
    font-family: 'Noto Sans CJK KR', sans-serif !important;
}

html {
    min-width: 300px;
    min-height: 300px;
    height: 100%;
}

body {
    height: 100%;
}

#root {
    height: 100%;
}

*:focus {
    box-shadow: none !important;
}

button[aria-selected=true] div div{
    filter: opacity(0.5) drop-shadow(0 0 0 cyan);
}

button[aria-selected=true] {
    border-bottom: 2px solid #36CAE0;
}

.textRightIcon {
    display: inline-flex;
}

.disabledBtn {
    background-color: gray !important;
    cursor: default;
}